
.history_box {
  position: relative;
  margin-top: 1.10933rem;
  border-top: 0.42667rem solid #f4f4f4;
}
.history_box .history_title {
  position: relative;
  margin-left: 0.53333rem;
}
.history_box .history_title p {
  text-align: left;
  color: #999;
  font-size: 0.59733rem;
  line-height: 1.96267rem;
}
.history_box .history_item {
  position: relative;
  height: 1.87733rem;
  margin-left: 0.53333rem;
  border-bottom: 0.02133rem solid #ccc;
}
.history_box .history_item::before {
  content: "";
  display: block;
  height: 0;
  clear: both;
}
.history_box .history_item .index_icon {
  position: relative;
  float: left;
  width: 0.68267rem;
  margin-top: 0.59733rem;
}
.history_box .history_item .index_icon img {
  display: block;
  width: 100%;
}
.history_box .history_item p {
  position: relative;
  float: left;
  width: 11.94667rem;
  overflow: hidden;
  margin-left: 0.384rem;
  font-size: 0.59733rem;
  line-height: 1.87733rem;
  color: #505050;
  text-align: left;
}
.history_box .index_removehistory {
  position: relative;
  margin: auto;
}
.history_box .index_removehistory p {
  text-align: center;
  color: #999;
  font-size: 0.59733rem;
  line-height: 1.96267rem;
}


