
.comment {
  padding-top: 1.024rem;
  background-color: #f3f3f3;
}
.comment h3 {
  margin-bottom: 0.08533rem;
  margin-left: 0.512rem;
  font-size: 0.64rem;
  font-weight: 400;
}
.comment .downLoad {
  display: block;
  margin: 0.128rem 0.512rem 0;
  height: 1.536rem;
  line-height: 1.536rem;
  font-size: 0.55467rem;
  color: #fff;
  background-color: #fb7299;
  border-radius: 0.128rem;
  text-align: center;
}
.comment .no_index_commentWrap p {
  text-align: center;
  padding: 1.536rem 0;
  font-size: 0.56933rem;
  color: #212121;
}
.comment .index_commentWrap {
  padding: 0 0.512rem;
}
.comment .index_commentWrap .index_commentItem {
  position: relative;
  padding: 0.768rem 0 0.66133rem 2.13333rem;
  min-height: 1.70667rem;
  border-top: 0.02133rem solid #ccc;
  overflow: hidden;
}
.comment .index_commentWrap .index_commentItem:first-child {
  border-top: none;
}
.comment .index_commentWrap .index_commentItem:first-child .index_imgWrap {
  position: absolute;
  width: 1.70667rem;
  height: 1.70667rem;
  top: 0.768rem;
  left: 0;
  border-radius: 50%;
  overflow: hidden;
}
.comment .index_commentWrap .index_commentItem:first-child .index_imgWrap .toux {
  width: 100%;
  height: 100%;
}
.comment .index_commentWrap .index_commentItem:first-child .index_upName {
  display: block;
  max-width: 60%;
  margin-bottom: 0.512rem;
  color: #757575;
  font-size: 0.55467rem;
}
.comment .index_commentWrap .index_commentItem:first-child .index_commentTime {
  position: absolute;
  top: 0.81067rem;
  right: 0;
  font-size: 0.46933rem;
  color: #999;
}
.comment .index_commentWrap .index_commentItem:first-child .index_commentTxt {
  color: #212121;
  line-height: 0.81067rem;
  font-size: 0.59733rem;
}


