
.focus {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 22;
  background: #fff;
}
.focus .focusnav {
  position: fixed;
  background-color: #fff;
  width: 100%;
  height: 1.87733rem;
  top: 1.856rem;
  z-index: 5;
  overflow: hidden;
}
.focus .focusnav .tab-left {
  position: relative;
  width: 100%;
  padding-top: 0.256rem;
  overflow: hidden;
}
.focus .focusnav .tab-left .left-scroll {
  position: relative;
  padding-left: 1.024rem;
  overflow-x: scroll;
  overflow-y: hidden;
  height: 1.87733rem;
  white-space: nowrap;
  padding-bottom: 0.21333rem;
}
.focus .focusnav .tab-left .left-scroll .a {
  display: inline-block;
  -webkit-transform: translateZ(0);
  -webkit-perspective: 1000;
  background-color: #fff;
  margin-right: 1.29333rem;
}
.focus .focusnav .tab-left .left-scroll .a p {
  width: 1.36533rem;
  font-size: 0.59733rem;
  line-height: 1.28rem;
  text-align: center;
}
.focus .focusnav .tab-left .left-scroll .active p {
  color: #fb7299;
  border-bottom: 0.08533rem solid #fb7299;
}
.focus .fix-top {
  position: fixed;
  z-index: 21;
  width: 100%;
  background: #fff;
}
.focus .fix-top .top-area {
  position: relative;
  width: 100%;
  height: 1.32267rem;
  padding: 0.27733rem 0;
  background-color: #fff;
}
.focus .fix-top .top-area .index_back {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  left: 0.42667rem;
  top: 50%;
  width: 1.536rem;
  height: 1.536rem;
  -webkit-transform: translateY(-50%);
}
.focus .fix-top .top-area .index_back img {
  position: relative;
  display: block;
  width: 25%;
  margin: auto;
}
.focus .fix-top .top-area .index_title {
  position: absolute;
  width: 2.26133rem;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
}
.focus .fix-top .top-area .index_title p {
  font-size: 0.68267rem;
  line-height: 1.024rem;
  color: #fb7299;
  text-align: center;
}
.focus .fix-top .top-area .index_vvbtn {
  position: absolute;
  display: block;
  top: 50%;
  -webkit-transform: translateY(-50%);
  right: 0.55467rem;
  width: 3.41333rem;
  height: 1.28rem;
  border: 0.04267rem solid #fb7299;
  border-radius: 0.17067rem;
}
.focus .fix-top .top-area .index_vvbtn p {
  font-size: 0.59733rem;
  line-height: 1.28rem;
  text-align: center;
  color: #fb7299;
}


