
.videolistbox {
  padding: 1.024rem 0 0;
  background-color: #f3f3f3;
}
.videolistbox h3 {
  padding-left: 0.512rem;
  margin-bottom: 0.42667rem;
  font-size: 0.64rem;
  font-weight: 400;
}
.videolistbox .index__loadMore {
  margin-top: 0.512rem;
  width: 100%;
  line-height: 1.728rem;
  font-size: 0.55467rem;
  color: #fb7299;
  background-color: #e7e7e7;
  text-align: center;
}
.videolistbox .list-box {
  padding: 0 0.512rem;
  overflow: hidden;
}
.videolistbox .list-box .item {
  display: block;
  position: relative;
  padding: 0.42667rem 0 0.42667rem 5.504rem;
  height: 3.11467rem;
  overflow: hidden;
  border-top: 0.02133rem solid #ccc;
}
.videolistbox .list-box .item .img_wrap {
  position: absolute;
  top: 0.42667rem;
  left: 0;
  width: 4.992rem;
  height: 3.11467rem;
  border-radius: 0.128rem;
  overflow: hidden;
}
.videolistbox .list-box .item .img_wrap img {
  width: 100%;
  height: 100%;
}
.videolistbox .list-box .item div .index_titlx {
  display: block;
  margin-top: -0.08533rem;
  margin-bottom: 0.17067rem;
  max-height: 1.536rem;
  line-height: 0.768rem;
  font-size: 0.59733rem;
  color: #212121;
  overflow: hidden;
}
.videolistbox .list-box .item div .index_upName {
  display: block;
  margin-bottom: 0.17067rem;
  line-height: 0.68267rem;
  font-size: 0.512rem;
  color: #757575;
}
.videolistbox .list-box .item .index_infowrap_s {
  line-height: 0.68267rem;
  font-size: 0.512rem;
  color: #757575;
}
.videolistbox .list-box .item .index_infowrap_s .index_num {
  float: left;
  width: 50%;
  line-height: 0.68267rem;
  font-size: 0.512rem;
  color: #757575;
}
.videolistbox .list-box .item .index_infowrap_s .index_view {
  float: left;
  width: 50%;
  line-height: 0.68267rem;
  font-size: 0.512rem;
  color: #757575;
}
.videolistbox .list-box .item:first-child {
  border-top: none;
}


